.picture__description {
  font-weight: 700;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  margin: 0;

}

.picture__author {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: capitalize;
  margin: 0;
}

.gallery__list {
  .list-reset()
}

.gallery__heading-wrapper {
  text-align: center;
  padding: 37px 20px 25px 20px;
}

.gallery__text,
.gallery__heading {
  margin: 0;
}

.gallery__heading {
  margin-bottom: 26px;
}

.gallery__item {
  padding: 25px 20px 25px 20px;
}

.gallery__video {
  margin: 17px 20px 0 20px;
}

.gallery__picture-tablet {
  display: none;
}

.gallery__picture-phone {
  display: block;
}

@media (min-width: 768px) {

  .gallery__heading-wrapper {
  .gallery__heading {
    font-size: 30px;
    line-height: 26px;
  }
  padding: 66px 155px 67px 155px;
  }

  .gallery__list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 60px 58px 60px;
  }

  .gallery__item {
    .gallery__picture {
      display: block;
      width: 324px;
      height: 267px;
    }
    padding: 0;
    margin: 0;
    width: 324px;
    height: 347px;
    background-color: #f2f2f2;
  }

  .gallery__item--large {
    width: 648px;
    height: 480px;
  }

  .gallery__picture-phone {
    display: none;
  }

    .gallery__picture-tablet {
    display: block;
  }
  .gallery__item {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .gallery__item:nth-child(3) {
    background-color: #EAEAEA;
  }

  .gallery__picture {
    width: 324px;
    height: 267px;
  }
}

@media (min-width: 1200px) {

  .gallery__heading-wrapper {
    padding: 100px 372px 65px 372px;
    }

  .gallery__list {
    margin: 0;
    margin-bottom: 69px;
  }

  .gallery__item {
    width: 33.3%;
    height: 267px;
  }

  .gallery__item {
    .gallery__picture {
      display: block;
      width: 400px;
      height: 267px;
    }
  }


  .gallery__item--large {
    width: 100%;
    height: 400px;
    .picture__text-wrapper{
      width: 1160px;
      padding-right: 20px;
    }
  }


  .gallery__picture-tablet {
    width: 100%;
  }
}
